.Multiselect {
  .Multiselect {
    &__control {
      min-height: 40px;
      border: solid 1px #e3e7ee;

      &:hover {
        border: solid 1px #e3e7ee;
      }

      &--is-focused,
      &--menu-is-open {
        box-shadow: none;
        border-width: 1px;
        border-color: #1cb6c1;

        &:hover {
          border: solid 1px #1cb6c1;
        }
      }
    }

    &__option {
      &--is-active {
        background-color: #dbf0f4;
      }
      &--is-focused {
        background-color: #dbf0f4;

        &:active {
          background-color: #dbf0f4;
        }
      }
    }

    &__multi-value__remove {
      &:hover {
        color: #fff;
        background-color: #1cb6c1;
        cursor: pointer;
      }
    }

    &__placeholder {
      color: #758eb1;
    }
  }
}
