.Content {
  flex-basis: 100%;
  position: relative;
  border-top: 1px solid #e3e7ee;
  margin-bottom: -18px;

  > div {
    width: 100%;
    height: 100%;
  }

  &::after {
    content: "";
    position: absolute;
    left:0;
    bottom: 0;
    width: 100%;
    height: 20px;
    background-color: #ffffff;
  }
}
